import React, { useState, useMemo, useRef } from 'react'

import { useLocation } from 'react-router-dom'

import { NavLabel } from 'components/Link/Link.style'

import { useMedia } from 'hooks/useMedia'
import { useOnClickOutside } from 'hooks/useOnClickOutside'

import { NavLink } from '../Link'
import { navMenuList } from './data'
import * as Styled from './Navbar.style'

export const Navbar = () => {
  const { pathname } = useLocation()

  const isMediaLg = useMedia('(min-width: 1238px)')

  const [isOpen, setOpen] = useState(false)
  const navRef = useRef(null)

  useOnClickOutside(navRef, () => {
    if (navRef && isOpen === true) {
      setOpen(false)
    }
  })

  // const handleOpen = React.useCallback(() => {
  //   setOpen(true)
  // }, [])

  // const handleClose = React.useCallback(() => {
  //   setOpen(false)
  // }, [])

  const toggleOpen = React.useCallback(() => {
    if (isOpen) setOpen(false)
    else setOpen(true)
  }, [isOpen])

  const renderMenu = useMemo(() => {
    return navMenuList
      .filter(({ isWideOnly }) => !isWideOnly || isMediaLg)
      .map(({ link, label }) => (
        <NavLink to={link} key={link} active={pathname === link}>
          {label}
        </NavLink>
      ))
  }, [pathname, isMediaLg])

  return (
    <nav ref={navRef}>
      <Styled.Container collapsed={false}>
        <Styled.Header>
          <Styled.Options onClick={toggleOpen} round>
            <span>. . .</span>
          </Styled.Options>
        </Styled.Header>
        {isOpen && (
          <Styled.MobileNavbar onClick={toggleOpen}>
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://crosschain.quest/"
            >
              <NavLabel size="big" weight="500">
                ABOUT
              </NavLabel>
            </a>
            {renderMenu}
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://discord.gg/fAf2ykVJSF"
            >
              <NavLabel size="big" weight="500">
                DISCORD
              </NavLabel>
            </a>
          </Styled.MobileNavbar>
        )}
      </Styled.Container>
    </nav>
  )
}
